import React from 'react';
const Register = ({changeR}) =>{
	return(
		<article className="br2 ba dark-gray b--black-10 mv4 w-100 w-50-m w-25-l mw5 shadow-5 center">
			<main className="pa4 black-80">
			  <form className="measure">
			    <fieldset id="sign_up" className="ba b--transparent ph0 mh0">
			      <legend className="f3 fw6 ph0 mh0">Register</legend>
			      <div className="mt3">
			        <label className="db fw6 lh-copy f6" for="email-address">Email</label>
			        <input className="pa2 input-reset ba bg-transparent hover-bg-black hover-white w-100" type="email" name="email-address"  id="email-address"/>
			      </div>
			      <div className="mv3">
			        <label className="db fw6 lh-copy f6" for="Name">Name</label>
			        <input className="b pa2 input-reset ba bg-transparent hover-bg-black hover-white w-100" type="text" name="Name"  id="Name"/>
			      </div>
			      <div className="mv3">
			        <label className="db fw6 lh-copy f6" for="password">password</label>
			        <input className="b pa2 input-reset ba bg-transparent hover-bg-black hover-white w-100" type="password" name="Name"  id="Name"/>
			      </div>
			    </fieldset>
			    <div className="">
			      <input onClick={()=>changeR('signin')} className="b ph3 pv2 input-reset ba b--black bg-transparent grow pointer f6 dib" type="submit" value="Register"/>
			    </div>
			    <div className="lh-copy mt3">
			      <p onClick={()=>changeR('signin')} href="#0" className="f6 link dim black db pointer">Sign In</p>
			    </div>
			  </form>
			</main>
		</article>
	);
}
export default Register;